.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  min-height: 200px;
}

.greeting {
  font-size: 2.5rem;
  margin-top: 50px;
  line-height: 3rem;
  font-weight: bold;
  letter-spacing: 0.5rem;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-shadow: -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white, 1px 1px 0 white;
}

.lamp {
  position: absolute;
  top: -20;
  height: auto;
  cursor: pointer;
  margin: 20px 0;
  transition: transform 0.2s ease-in-out;
}

.subtitle {
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  color: #eaeaea;
  margin-bottom: 10px;
  text-shadow: 0px 4px 10px rgba(255, 255, 255, 0.5);
}

.instruction {
  position: absolute;
  top: 230;
  font-size: 1.2rem;
  color: #666;
  text-align: center;
  line-height: 1.5;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .container {
    padding: 15px;
  }
  .greeting {
    font-size: 2rem;
  }
  .instruction {
    font-size: 1rem;
  }
}/*# sourceMappingURL=greeting.module.css.map */